<template>
  <div class="main">
    <div class="main-wrapper">
    <TitleTask
      v-if="!isModal"
      :title='mainDataList[numberTask].title'
    />
    <br>
    <Info
      v-if="mainDataList[numberTask].typeTask=='info'"
      :bodyInfo="mainDataList[numberTask].dataTask.bodyTask"
      class="body-game"
    />
    <Researches
      v-if="mainDataList[numberTask].typeTask=='researches'"
      :researchList="mainDataList[numberTask].dataTask"
      :isModal="isModal"
      @truemodal="openModal()"
      @checkedmodal="checkedModal($event)"
      class="body-game"
    />
    <br>
    <Test
      v-if="mainDataList[numberTask].typeTask=='test'"
      :dataTask="mainDataList[numberTask].dataTask"
      :isValidate="isValidate"
      class="body-game"
    />
    <br>
    <Navigation
      :numberTask='numberTask'
      :typeTask='mainDataList[numberTask].typeTask'
      :finalNumberTask='mainDataList.length-1'
      :isModal="isModal"
      :isValidate="isValidate"
      @previustask=previusTask()
      @nexttask=nextTask()
      @firsttask=firstTask()
      @closemodal=closeModal()
    />    
  </div>
  </div>
</template>

<script>
import TitleTask from './components/TitleTask'
import Info from './components/Info'
import Researches from './components/Researches'
import Navigation from './components/Navigation'
import Test from './components/Test'

export default {
  name: 'App',
  components: {
    TitleTask,
    Info,
    Researches,
    Navigation,
    Test
  },
  data() {
    return{
      numberTask: 0,
      isModal: false,
      mainDataList:[
        { 
          typeTask:'info',
          title:'Общая информация',
          dataTask:{
            bodyTask: 'Мужчина, 25 лет обратился к неврологу в поликлинику по месту жительства'
          }
        },
        {
          typeTask:'researches',
          title:'Первичный осмотр',
          dataTask:
            [
              {
                title: 'Жалобы',
                bodyTask: 'Жалобы на интенсивные ноющие боли в пояснично-крестцовом отделе позвоночника, в области копчика, усиливающиеся при ходьбе, кашле, чихании',
                imgTask:'imgii1.png',
                isChecked: false
              },
              {
                title: 'Объективно',
                bodyTask: 'Общее состояние удовлетворительное. Температура тела 36,7 С. Кожные покровы обычной окраски. Периферические лимфоузлы не увеличены',
                imgTask:'imgii2.png',
                isChecked: false
              },
            ]           
        },
        {
          typeTask:'test',
          title:'Дифференциальный диагноз',
          dataTask:
            [
              {
                answer: 'Аденома предстательной железы',
                isChecked: false,
                isTrue: false
              },
              {
                answer: 'Внутрипозвоночная опухоль',
                isChecked: false,
                isTrue: false
              },
              {
                answer: 'Грыжа бедренная',
                isChecked: false,
                isTrue: true
              }
            ]           
        },
        { 
          typeTask:'info',
          title:'Общая информация',
          dataTask:{
            bodyTask: 'Завершено. Вы молодец!'
          }
        }
      ]
    }
  },
  methods: {
    previusTask() {
      if (this.numberTask>0) {
        this.numberTask--
      }
    },
    nextTask() {
      if (this.numberTask<(this.mainDataList.length-1)){
        this.numberTask++
      }
    },
    firstTask() {
      this.numberTask = 0;      
    },
    openModal() {
      this.isModal = true;
    },
    closeModal() {
      this.isModal = false;
    },
    checkedModal(index) {
      this.mainDataList[this.numberTask].dataTask[index].isChecked = true
    }
  },
  computed: {
    isValidate() {
      if (this.mainDataList[this.numberTask].typeTask == "info") {
        return true
      }
      else if (this.mainDataList[this.numberTask].typeTask == "researches") {
        for (let i=0 ; i<this.mainDataList[this.numberTask].dataTask.length;i++){
          if (!this.mainDataList[this.numberTask].dataTask[i].isChecked) {
            return true //Нужно поставить false
          }
        }        
        return true
      }
      else if (this.mainDataList[this.numberTask].typeTask == "test") {
        for (let i=0 ; i<this.mainDataList[this.numberTask].dataTask.length;i++){
          if (this.mainDataList[this.numberTask].dataTask[i].isChecked !==  this.mainDataList[this.numberTask].dataTask[i].isTrue) {
            return false
          }
        }        
        return true
      }

      else {
        return true
      }
    }
  },
  mounted(){
    // console.log(this.componentList)
  }
}
</script>

