<template>
  <div class="navigation-buttons">
    <br>
      <el-button round
        v-if="numberTask>0 && !isModal"
        @click="previusTask"
      >
      Назад
      </el-button>

      <el-button round
        v-if="numberTask<finalNumberTask && !isModal"
        @click="nextTask"
      >
      Далее
      </el-button>

      <el-button round
        v-if="numberTask==finalNumberTask && !isModal"
        @click="startAgain"
      >
      Начать заново
      </el-button>

      <el-button round
        v-if="isModal"
        @click="closeModal()"
      >
      Закрыть
      </el-button>
  </div>
</template>

<script>
export default {
    name: 'Navigation',
    props: {
        numberTask: Number,
        finalNumberTask: Number,
        typeTask: String,
        isModal: Boolean,
        isValidate: Boolean
    },
    methods: {
    previusTask() {
      this.$emit("previustask")
    },
    nextTask() {
      if (this.isValidate)  {
        this.$emit("nexttask")
      }
    },    
    startAgain() {
      this.$emit("firsttask")
    },
    closeModal() {
      this.$emit("closemodal")
    }
  }
}
</script>

<style scoped>
  .navigation-buttons {
    text-align: center;
  }
</style>