<template>
    <div class="research-icons">
        <div 
            class="cube-wrapper"
            v-if="!isModal"
            >
                <div
                    class="small-cube"
                    v-for="(researchObj, index) in researchList"
                    :key="index"
                    @click="openModal(index)"
                >
                    <img src="./../assets/img/imgii1.png">
                    <div>
                        {{researchObj.title}}
                    </div>
                </div>
        </div>
        <TitleTask
            v-if="isModal"
            :title='researchList[numberModal].title'
        />
        <Info
            v-if="isModal"
            :bodyInfo="researchList[numberModal].bodyTask"
        />
        
    </div>
</template>

<script>
import TitleTask from './TitleTask'
import Info from './Info'

export default {
    name:'Researches',
    components: {
        TitleTask,
        Info
    },
    data() {
        return {
            numberModal: 0
        }
    },
    props: {
        researchList: [],
        isModal: Boolean
    },
    methods: {
        openModal(index){
            this.numberModal = index
            this.$emit('truemodal')
            this.$emit('checkedmodal', index)
        }
    }
}
</script>

<style scoped>
    .cube-wrapper{
        display: flex;
        justify-content: center;
    }
    
    .small-cube {
        width: 100px;
        height: 100px;
        border: 1px solid black;
        border-radius: 5px;
        margin: 2px;
    }

</style>